import { useState } from 'react';
import { Icon } from '@iconify/react';
import downloadFill from '@iconify/icons-eva/download-fill';
// material
import {
  Box,
  Card,
  Grid,
  Stack,
  Slider,
  Button,
  FormLabel,
  Container,
  Typography,
  CardHeader
} from '@material-ui/core';
// components
import Page from '../components/Page';
import ColorPicker from '../components/ColorPicker';
import { MHidden, DHidden } from '../components/@material-extend';

// ----------------------------------------------------------------------

const Flag = () => <canvas id="flag" style={{ width: '100%', height: '100%' }} />;

export default function FlagMaker() {
  const [stripes, setStripes] = useState(7);
  const [colors, setColors] = useState([]);

  function redraw() {
    const canvas = document.querySelector('canvas#flag');
    if (!canvas) return;

    canvas.style.height = '100%';

    canvas.width = 1920;
    canvas.height = 1080;

    const ctx = canvas.getContext('2d');

    ctx.clearRect(0, 0, canvas.width, canvas.height);

    for (let i = 0; i < stripes; i += 1) {
      const color = colors[i];
      const height = Math.floor(canvas.height / stripes + 1);
      ctx.beginPath();
      ctx.fillStyle = color;
      ctx.fillRect(0, height * i, canvas.width, height);
      ctx.stroke();
    }
  }

  function stripeChange(e) {
    setStripes(e.target.value);
  }

  function colorChange(e) {
    setColors(e);
    redraw();
  }

  const limits = [5, 11];
  const marks = [];

  for (let i = limits[0]; i <= limits[1]; i += 2) {
    marks.push({
      value: i,
      label: i.toString()
    });
  }

  return (
    <Page title="Flag Maker">
      <Container>
        <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
          <Typography variant="h4" gutterBottom>
            Flag Maker
          </Typography>
        </Stack>

        <DHidden width="lgUp">
          <Grid container spacing={1}>
            <Grid item xs={7}>
              <Card>
                <CardHeader title="Flag" />
                <Box sx={{ p: 3, pb: 1 }} dir="ltr">
                  <Flag stripes={stripes} colors={colors} />
                  <img src="" alt="" onError={redraw} />
                  <a id="link">&nbsp;</a>
                  <Button
                    variant="contained"
                    mx={5}
                    onClick={() => {
                      const canvas = document.querySelector('canvas#flag');

                      const link = document.getElementById('link');
                      link.setAttribute('download', 'flag.png');
                      link.setAttribute(
                        'href',
                        canvas.toDataURL('image/png').replace('image/png', 'image/octet-stream')
                      );
                      link.click();
                    }}
                    startIcon={<Icon icon={downloadFill} />}
                  >
                    Download
                  </Button>
                </Box>
                <br />
              </Card>
            </Grid>
            <Grid item xs={5}>
              <Card>
                <CardHeader title="Settings" />
                <Box sx={{ p: 3, pb: 1 }} dir="ltr">
                  <FormLabel id="stripes-label">Stripes: {stripes}</FormLabel>
                  <Slider
                    min={limits[0]}
                    max={limits[1]}
                    step={2}
                    value={stripes}
                    onChange={stripeChange}
                    onChangeCommitted={redraw}
                    aria-labelledby="stripes-label"
                    marks={marks}
                  />
                  <ColorPicker stripes={stripes} onChange={colorChange} />
                </Box>
                <br />
              </Card>
            </Grid>
          </Grid>
        </DHidden>

        <MHidden width="lgUp">
          <Grid container direction="column" spacing={1}>
            <Grid item xs={8}>
              <Card>
                <CardHeader title="Settings" />
                <Box sx={{ p: 3, pb: 1 }} dir="ltr">
                  <FormLabel id="stripes-label">Stripes: {stripes}</FormLabel>
                  <Slider
                    min={limits[0]}
                    max={limits[1]}
                    step={2}
                    value={stripes}
                    onChange={stripeChange}
                    onChangeCommitted={redraw}
                    aria-labelledby="stripes-label"
                    marks={marks}
                  />
                  <ColorPicker stripes={stripes} onChange={colorChange} />
                </Box>
                <br />
              </Card>
            </Grid>
            <Grid item xs={8}>
              <Card>
                <CardHeader title="Flag" />
                <Box sx={{ p: 3, pb: 1 }} dir="ltr">
                  <Flag stripes={stripes} colors={colors} />
                  <img src="" alt="" onError={redraw} />
                  <a id="link">&nbsp;</a>
                  <Button
                    variant="contained"
                    mx={5}
                    onClick={() => {
                      const canvas = document.querySelector('canvas#flag');

                      const link = document.getElementById('link');
                      link.setAttribute('download', 'flag.png');
                      link.setAttribute(
                        'href',
                        canvas.toDataURL('image/png').replace('image/png', 'image/octet-stream')
                      );
                      link.click();
                    }}
                    startIcon={<Icon icon={downloadFill} />}
                  >
                    Download
                  </Button>
                </Box>
                <br />
              </Card>
            </Grid>
          </Grid>
        </MHidden>
      </Container>
    </Page>
  );
}
